.hotel {
    padding: 270px 0px 200px 0px;
    &__title {
        margin-bottom: 105px;
        span {
            display: block;
            font-family: Roboto Condensed;
            font-style: italic;
            font-weight: 300;
            font-size: 24px;
            color: #000000;
            margin-left: 120px;
            margin-top: -20px;

        }
    }
    &__wrap {
        display: flex;
    }
    @media (max-width: 1200px) {
        .hotel__wrap {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        .hotel__content {
            margin: 0;
            margin: 50px 10px;
            max-width: 600px;

        }
        .hotel__gallery {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        
    }
    &__content {
        margin-left: 100px;
        max-width: 600px;
        height: 100%;
    }
    &__img {
        max-width: 600px;
        img {
            width: 100%;
        }
       
    }
    @media(max-width:550px) {
        .hotel__img {
            max-width: 300px;
        }
    }

    &__carts {
        display: flex;
        flex-wrap: wrap;
        
    }
    &__text {
        margin-bottom: 30px;
        font-family: Roboto Condensed;
        font-style: normal;
        font-weight: 300;
        font-size: 18px;
        line-height: 21px;
        color: #000000;
    }
    &__gallery {
        a {
            text-decoration: none;
            img {
                padding: 8px;
            }
        }
    }
}
