.price {
    &__title {
        margin-bottom: 40px;
        span {
            display: block;
            font-family: Roboto Condensed;
            font-style: italic;
            font-weight: 300;
            font-size: 24px;
            line-height: 28px;
            color: #000000;
            margin-left: 120px;
            margin-top: -20px;
        }
    }
    &__container {
        max-width: 1315px;
        margin: 0 auto;
    }
    &__wrap {
        display: flex;
        justify-content: space-between;
        padding: 0px 10px; 
    }
    @media (max-width: 1050px) {
        .price__wrap{
        flex-direction: column;
        align-items: center;
       }
       .price__content{
           margin-bottom: 50px;
       }
    }
    &__content {
        flex: 0 1 33.333%;
        max-width: 410px;
        ul {
            list-style: none;
            li {
                font-family: Roboto Condensed;
                font-style: normal;
                font-weight: 300;
                font-size: 18px;
                color: #000000;
                margin-bottom: 20px;

            }
        }
    }
    &__allin, &__allnot {
        font-family: Roboto Condensed;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        line-height: 32px;
        color: #000000;
        margin-bottom: 50px;
        flex-grow: 1;
    }
    &__decor {
        max-width: 329px;
        img {
            width: 100%;
        }
    }
}