.ourtrip {
    &__title {
        margin-bottom: 110px;
        span {
            display: block;
            font-family: Roboto Condensed;
            font-style: italic;
            font-weight: 300;
            font-size: 24px;
            color: #000000;
            margin-top: -17px;
            margin-left: 240px;
        }
    }
    &__day {
            font-family: Roboto Condensed;
            font-style: normal;
            font-weight: normal;
            font-size: 24px;
            line-height: 32px;
            color: #000000;
            margin-bottom: 20px;
            margin-left: 10px;
    }
    &__list {
        list-style: none;
        margin-bottom: 50px;
        li {
            margin-left: 10px;
        font-family: Roboto Condensed;
        font-style: normal;
        font-weight: 300;
        font-size: 24px;
        line-height: 28px;
        color: #000000;
        }
    }
    &__button {
        display: flex;
        justify-content: center;
        margin: 0 auto;
    }
    .ourtrip-slider {
        position: relative;
        max-width: 960px;
    }
    @media (max-width: 1570px)  {
    .ourtrip-slider {
        max-width: 600px;
        order: 1;
    }
}
    @media (max-width:1200px) {
        .ourtrip__wrap {
            flex-direction: column;
            align-items: center;
        }
        .ourtrip__content {
            flex-direction: column;
            align-items: center;
            margin: 0;
            margin-bottom: 50px;
            order: 2;
        }
        
    }
    @media (max-width: 770px)  {
        .ourtrip-slider {
            max-width: 500px;
        }
        }
        @media (max-width: 550px)  {
            .ourtrip-slider {
                max-width: 300px;
            }
            }
    &__wrap {
        display: flex;
        justify-content: center;
    }
    &__content {
        margin-right: 275px;
    }

}

