.fairy {
    padding-top: 200px;
    &__title {
        margin-bottom: 100px;
        
        span {
            display: block;
            font-family: Roboto Condensed;
            font-style: italic;
            font-weight: 300;
            font-size: 24px;
            color: #000000;
            margin-top: -20px;
            margin-left: 185px;
        }

    }
    .fairy-slider {
        position: relative;
        max-width: 960px;
        
    }
    @media (max-width: 1570px) {
        .fairy-slider {
            max-width: 600px;
        }
    }
    @media (max-width:1200px) {
        .fairy__inner {
            flex-direction: column;
            align-items: center;
        }
        .fairy__content{
            margin: 20px auto;
        }
        
    }
    @media (max-width: 770px)  {
        .fairy-slider {
            max-width: 500px;
        }
        }
        @media (max-width: 550px)  {
            .fairy-slider {
                max-width: 300px;
            }
            }
    &__inner {
        display: flex;
        justify-content: space-between;
        margin-bottom: -3.5px;
    }
    &__content {
        max-width: 545px;
        margin-right: auto;
        margin-left: 55px;
    }
    &__text {
        font-family: Roboto Condensed;
        font-style: normal;
        font-weight: 300;
        font-size: 18px;
        line-height: 21px;
        color: #000000;
        margin-bottom: 50px;

    }
    &__aussi {
        font-family: Roboto Condensed;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        color: #000000;
        margin-bottom: 50px;
    }
    &__list {
        list-style: none;
        margin-bottom: 50px;
        li {
        font-weight: 300;
        margin-bottom: 5px;
        }
    }
}
.slick-list {
    overflow: hidden;
    width: 100%;
}
.slick-track{
	display: flex;
	align-items: flex-start;
}
.slick-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border: none;
    background: none;
    z-index: 5;
    width: 50px;
    opacity: 0.8;
    img {
        width: 100%;
    }
}
.slick-left {
    left: 0;
}
.slick-next {
    right: 0;
}
 