h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}
* {
    padding: 0;
    margin: 0;
    outline: none;
    box-sizing: border-box;
    text-decoration: none;
    
}
body {
    font-family: 'Roboto Condensed', sans-serif;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
}
.title {
    font-family: 'Balqis', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 96px;
    color: #E2AB3F;
}

.button {
    max-width: 280px;
    height: 50px;
    background: #FFFFFF;
    border: 1px solid #E2AB3F;
    font-family: Roboto Condensed;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    color: #000000;
    padding: 2px 20px;
    outline: none;

}
 

@media(max-width:1050px) { 
    .title {
        text-align: center;
        font-size: 80px;
        span {
            margin-top: 0;
            margin-left: 110px;
        }
    }
    
}