.contacts {
    padding-top: 100px; 
    height: 100vh;
    background: url('../img/svg/bgcontact.jpg') center (center/cover) no-repeat;
    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__text {      
        font-family: 'Balqis', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 86px;
        color: #000000;
    }
    &__fly {
        width: 356px;
        height: 576px;
        background: url('../img/svg/shar.png') center no-repeat;
        display: flex;
        text-align: center;
        flex-direction: column;
        align-items: center;
    }
    &__title {
        padding: 76px 5px 0px 21px;
        margin-bottom: 50px;
        font-size: 86px;
    }
    &__phone, &__email {
        margin-bottom: 20px;
        text-decoration: none;
        font-family: Roboto Condensed;
        font-weight: 300;
        font-size: 18px;
        color: #000000;
        position: relative;
        display: block;
    } 
    &__phone::before {
        content: '';
        position: absolute;
        top: -2px;
        left: -35px;
        width: 50px;
        height: 50px;
        background: url('../img/svg/phone.svg') no-repeat;
    }
    &__email::before {
        content: '';
        position: absolute;
        top: -2px;
        left: -35px;
        width: 50px;
        height: 50px;
        background: url('../img/svg/mail.svg') no-repeat;
    }
    &__social {
        margin-top: 40px;
        ul {
            display: flex;
            list-style: none;
            li {
                margin: 0px 10px;
            }
        }
    }
}

@media (max-width: 1200px) {
    .contacts__wrap{
        margin: 0px 20px;
    }
    .contacts__text {
        font-size: 70px;
    }
}
@media (max-width: 900px) {
    .contacts__wrap{
        flex-direction: column;
    }
    .contacts__fly {
        order: 2;
    }
    .contacts__text {
        order: 1;
    }
}

@media (max-width: 525px) {
    .contacts__social {
        ul{ 
            li {           
                margin: 0px 3px;
            }
        }
    }
    .contacts__text {
        font-size: 40px;
    }
    .contacts__title {
        padding: 135px 5px 0px 21px;
        font-size: 66px;
        margin-bottom: 10px;
    }
    .contacts__fly {
        width: 320px;
        padding: 0;
        background-size: 80%;
    }
    .contacts__phone, .contacts__email {
        font-size: 16px;
        padding-left: 20px;
        &::before {
            width: 30px;
            height: 30px;
            left: -5px;
            background-size: 70%;
        }
    }
}

