.header {
    padding: 35px 0px;
    position: fixed;
    width: 100%;
    z-index: 100;
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0px 10px;
    }
    &__phone {
            text-decoration: none;
            font-size: 24px;
            color: #050505;
            position: relative;
            &::before {
                content: '';
                position: absolute;
                display: block;
                background: url('../img/svg/phone.svg') center no-repeat;
                top: 0;
                left: -35px;
                width: 30px;
                height: 30px;
            }
    }
}
.header__menu {
    display: block;
    width: 50px;
    height: 50px;
    background-color: #050500;
    border-radius: 50%;
    position: relative;
    cursor: pointer;
    display: none;
    margin-right: 10px;
    opacity: 0.8;
}
.header__hamburger,.header__hamburger::after,.header__hamburger::before {
    position: absolute;
    top: 50%;
    margin-top: -1px;
    left: 50%;
    margin-left: -10px;
    width: 20px;
    height: 2px;
    background-color: #fff
}

.header__hamburger::after,.header__hamburger::before {
    content: '';
    display: block;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s
}

.header__hamburger::before {
    -webkit-transform: translateY(-5px);
    -ms-transform: translateY(-5px);
    transform: translateY(-5px)
}

.header__hamburger::after {
    -webkit-transform: translateY(5px);
    -ms-transform: translateY(5px);
    transform: translateY(5px)
}

.header__hamburger-active:before {
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg)
}

.header__hamburger-active:after {
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg)
}

.header__hamburger-active {
    height: 0
}
@media (max-width:768px) {
    .header__menu {
        display: block;
    }
}

.menu {
    &__list {
        display: flex;
        list-style: none;
    }
    &__list-item {
        margin-right: 50px;
        a {
            text-decoration: none;
            font-size: 24px;
            color: #050505;
        }
    }
}


.header-active {
    background-color: rgba(#050505, 0.2);
}

@media (max-width: 768px) {
    .menu {
        display: none;
    }

}