.order {
    padding: 60px 0px 80px 0px;
    &__wrap {
        max-width: 1315px;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__content {
        display: flex;
        flex-direction: column;
        margin-right: 115px;
    }
    &__form {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    &__date {
        span {
            font-family: Roboto Condensed;
            font-style: italic;
            font-weight: 300;
            font-size: 24px;
            line-height: 28px;
            color: #000000;
            display: block;
            margin-left: 120px;
            margin-top: -15px;
            margin-bottom: 20px;
            
        }
    }
    &__price {
        text-align: center;
        span {
            font-family: Roboto Condensed;
            font-style: italic;
            font-weight: 300;
            font-size: 24px;
            line-height: 28px;
            color: #000000;
            display: block;
            margin-left: 190px;
            margin-top: -15px;
            margin-bottom: 20px;
            
        }
    }
    &__img {
        img {
            width: 100%;
        }
    }
    &__question {
        font-family: Roboto Condensed;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        line-height: 32px;
        color: #000000;
        text-align: center;
        margin-bottom: 50px;
    }
    &__form {
        input:not([type='submit']) {
            width: 383px;
            height: 50px;
            background: #FFFFFF;
            border: 1px solid #E2AB3F;
            margin-bottom: 20px;
            padding: 15px;
        }
    }
    @media(max-width:400px) {
        .order__form {
            input{
                width: 300px;
            }
        }
    }
}
@media(max-width:768px) {
    .order__wrap {
        flex-direction: column;
        align-items: center;
    }
    .order__content {
        margin: 0;
        margin-bottom: 50px;
        order: 2;
    }
    .order__img {
        order: 1;
    }
    @media(max-width:550px) {
        .order__img {
            max-width: 300px;
        }
    }
}

