.principale {
    height: 100vh;
    background: url('../img/svg/bg.jpg') center (center/cover) no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    &__title {
        font-family: 'balqis', sans-serif;
        font-size: 250px;
        font-weight: normal;
        padding: 0px 20px;
    }
    &__subtitle {
        font-style: italic;
        font-weight: 300;
        font-size: 56px;
        text-align: right;
        padding: 0px 20px;
    }
}

@media(max-width:1050px) {
    .principale__title {
        font-size: 150px;
    }
    .principale__subtitle {
        font-size: 35px;
    }

}
@media(max-width:650px) {
    .principale__title {
        font-size: 90px;
        padding: 0px;
    }
    .principale__subtitle {
        font-size: 25px;
    }
}
@media(max-width:400px) {
    .principale__title {
        font-size: 80px;
    }
    .principale__subtitle {
        font-size: 20px;
    }
}
@media (max-width: 340px) {
    .principale__title {
        font-size: 70px;
    }
}